.btnGroup {
    display: flex;
    justify-content: space-between;
    align-items: center;

}


.btn {
    padding: 20px;
    font-size: 20px;
    margin: 10px;
    width: 25%;
    border-radius: 20px;
    border: none;
    background-color: rgb(195, 197, 199);
    cursor: pointer;
    transition: all 0.2s;
}

.btn:hover {
    background-color: rgb(168, 170, 173);
}

.btn:first-child {
    margin-left: 0;
}

.btn:last-child {
    margin-right: 0;
}

.btnTwo {
    width: calc(50% + 45px);
}